$qualitySpanPosition: -10px;

.root {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  .top {
    border-bottom: 1px solid #000;
    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: calc(60px + 20 * (100vw / 1200));
      .nav {
        .linkToCart {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          transition: all 0.1s;
          span {
            top: $qualitySpanPosition;
            right: $qualitySpanPosition;
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 2px 8px;
            border-radius: 50%;
            background: #8ff894;
            font-weight: 600;
            font-size: calc(14px + 2 * (100vw / 1200));
            line-height: normal;
            color: #222;
          }
          &:hover {
            opacity: 0.8;
          }
        }
      }
    }
  }
  .bottom {
    border-bottom: 1px solid #000;
    background: #f0f0f0;
    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: calc(40px + 20 * (100vw / 1200));
    }
  }
}
@media (max-width: 991px) {
  .root {
    .top {
      .container {
        .nav {
          .linkToCart {
            &:hover {
              opacity: unset;
            }
          }
        }
      }
    }
  }
}
