@import "../../assets/scss/variables.scss";

.root {
  position: relative;
  .showSortButton {
    display: flex;
    align-items: center;
    background: none;
    gap: calc(10px + 10 * (100vw / 1200));
    font-weight: 600;
    font-size: $filterPopupFontSize;
    line-height: normal;
    color: #000;
    span {
      transition: all 0.3s;
    }
    &:hover {
      span {
        background: rgba(#000, 0.08);
        translate: 0px 3px;
      }
    }
    span.active {
      rotate: 180deg;
    }
  }
  .popup {
    right: 0px;
    top: calc(33px + 10 * (100vw / 1200));
    position: absolute;
    width: $filterPopupWidth;
    height: $filterPopupHeight;
    background: #f0f0f0;
    border: 1px solid #000;
    border-top: none;
    overflow: hidden;
    z-index: 999;
    .list {
      height: 100%;
      display: grid;
      grid-template-rows: repeat(4, 1fr);
      .listItem {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        width: 100%;
        font-weight: 400;
        font-size: $filterPopupFontSize;
        line-height: normal;
        color: #000;
        transition: $filterPopupItemTransition;
        &:hover {
          background: $filterPopupItemHover;
        }
      }
      .listItem.active {
        background: $filterPopupItemHover;
      }
    }
  }
  .popupEnter {
    height: 0px;
    opacity: 0;
  }
  .popupEnterActive {
    height: $filterPopupHeight;
    opacity: 1;
    transition: all 0.35s ease-out;
  }
  .popupExit {
    height: $filterPopupHeight;
    opacity: 1;
  }
  .popupExitActive {
    height: 0px;
    opacity: 0;
    transition: all 0.35s ease-out;
  }
}
@media (max-width: 991px) {
  .root {
    .showSortButton {
      &:hover {
        span {
          background: rgba(#000, 0.1);
          translate: unset;
        }
      }
    }
    .popup {
      .list {
        .listItem {
          &:hover {
            background: unset;
          }
        }
      }
    }
  }
}
