@import "../../assets/scss/variables.scss";

$add-to-cart-button-size: calc(30px + 10 * (100vw / 1200));

.root {
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  &:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
    translate: 0px -5px;
  }
  .image {
    width: 100%;
    background: #eaeaea;
    height: calc(150px + 70 * (100vw / 1200));
    img {
      width: auto;
      display: block;
      height: 100%;
      margin: 0px auto;
    }
  }
  .info {
    padding: 10px 5px;
    h4 {
      margin-bottom: 10px;
    }
    .details {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .detailsWrapp {
        span {
          display: block;
        }
        span {
          margin-bottom: 10px;
        }
      }
      .addToCartButton {
        display: flex;
        align-items: center;
        justify-content: center;
        width: $add-to-cart-button-size;
        height: $add-to-cart-button-size;
        background: none;
        border-radius: 5px;
        transition: background 0.2s ease-in-out;
        &:hover {
          background: rgba(#000, 0.05);
        }
      }
      .addToCartButton.buttonPreloader {
        background: rgba(#000, 0.05);
      }
      .addToCartButton.added {
        background: #8ff894;
      }
    }
  }
}
@media (max-width: 991px) {
  .product {
    &:hover {
      box-shadow: none;
      translate: unset;
    }
    .info {
      .details {
        .addToCartButton {
          &:hover {
            background: none;
          }
        }
        .addToCartButton.added {
          background: #8ff894;
        }
      }
    }
  }
}
