.overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background: rgba(#000, 0.2);
  z-index: 999;
  .body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    width: 400px;
    height: 200px;
    background: #fff;
    border-radius: 20px;
    padding: 30px 20px;
    z-index: 999;
    .title {
      font-weight: 700;
      font-size: calc(16px + 2 * (100vw / 1200));
      text-align: center;
      line-height: normal;
      color: #222;
    }
    .buttons {
      display: flex;
      justify-content: center;
      gap: 50px;
      .button {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 120px;
        height: 40px;
        border-radius: 10px;
        font-weight: 600;
        font-size: calc(14px + 2 * (100vw / 1200));
        color: #000;
        transition: all 0.1s ease-in-out;
        &:active {
          translate: 0px 0.5px;
        }
      }
      .buttonConfirm {
        background: rgba(#b5cae4, 0.7);
        &:hover {
          background: rgba(#b5cae4, 1);
        }
      }
      .buttonCancel {
        background: rgba(#d3d3d3, 0.7);
        &:hover {
          background: rgba(#d3d3d3, 1);
        }
      }
    }
  }
}
@media (max-width: 480px) {
  .overlay {
    .body {
      width: 290px;
    }
  }
}
