@import "reset";
@import "variables";

$cartIconWidth: calc(23px + 7 * (100vw / 1200));
$cartIconHeight: calc(20px + 7 * (100vw / 1200));
$cross-icon-size: calc(15px + 15 * (100vw / 1200));
$arrowCircleSize: calc(20px + 5 * (100vw / 1200));

body {
  font-family: "Open Sans";
  color: #222222;
  &::-webkit-scrollbar {
    width: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #000;
  }
  &::-webkit-scrollbar-track {
    background: #aaaaaa;
  }
}
.wrapper {
  min-height: 100%;
  overflow: hidden;
}
.container {
  max-width: 1230px;
  margin: 0px auto;
  padding: 0px 15px;
}

.logo {
  font-weight: 700;
  font-size: calc(18px + 6 * (100vw / 1200));
  line-height: normal;
}

.cart-icon {
  width: $cartIconWidth;
  height: $cartIconHeight;
}
.cross-icon {
  width: $cross-icon-size;
  height: $cross-icon-size;
}
.arrow-circle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: $arrowCircleSize;
  height: $arrowCircleSize;
  background: rgba(#000, 0.1);
  border-radius: 50%;
}
.section-title {
  text-align: center;
  font-weight: 700;
  font-size: calc(20px + 4 * (100vw / 1200));
  line-height: normal;
  color: #222222;
}
.cart-icon {
  width: calc(20px + 10 * (100vw / 1200));
  height: calc(17px + 10 * (100vw / 1200));
}
.filter-popup_list {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
}
.product-price {
  display: block;
  font-weight: 700;
  font-size: 15px;
  line-height: normal;
  color: #222;
}
.product-title {
  font-weight: 700;
  font-size: 16px;
  line-height: normal;
  color: #222;
}
.product-brand {
  display: block;
  font-weight: 600;
  font-size: 14px;
  line-height: normal;
  color: rgba(#000, 0.5);
}
.error-message {
  margin-top: 25px;
  font-weight: 500;
  font-size: calc(16px + 2 * (100vw / 1200));
  line-height: normal;
  text-align: center;
}

.circle-preloader {
  display: block;
  width: 70px;
  height: 70px;
  border: 3px solid #ffffff;
  border-bottom-color: #000000;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;

  animation: spin 0.75s infinite linear;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
