$filterPopupFontSize: calc(14px + 2 * (100vw / 1200));
$filterPopupItemHover: rgba(#000, 0.08);
$filterPopupItemTransition: background 0.15s ease;
$filterPopupWidth: calc(180px + 40 * (100vw / 1200));
$filterPopupHeight: calc(160px + 40 * (100vw / 1200));
$mainPageMarginTop: calc(150px + 70 * (100vw / 1200));
$pageMarginTop: calc(110px + 30 * (100vw / 1200));
