$button-size: calc(25px + 10 * (100vw / 1200));

.root {
  display: flex;
  align-items: center;
  height: calc(120px + 80 * (100vw / 1200));
  border: 1px solid rgba(#000, 1);
  border-left: none;
  border-right: none;
  padding: 5px;
  position: relative;
  margin-bottom: 30px;
  .deleteButton {
    width: $button-size;
    height: $button-size;
    top: 15px;
    right: 15px;
    position: absolute;
    background: none;
    border-radius: 50%;
    transition: all 0.15s;
    &:hover {
      background: #ff8a8a;
    }
    &:active {
      translate: 0 1px;
    }
  }
  .image {
    width: calc(120px + 80 * (100vw / 1200));
    height: 100%;
    background: #eaeaea;
    img {
      margin: 0px auto;
      display: block;
      height: 100%;
      width: 65%;
    }
  }
  .info {
    padding: calc(8px + 7 * (100vw / 1200));
    display: flex;
    flex-direction: column;
    gap: calc(10px + 30 * (100vw / 1200));
  }
  .counter {
    display: flex;
    align-items: center;
    gap: calc(5px + 15 * (100vw / 1200));
    margin-left: auto;
    .count {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 20px;
      font-weight: 700;
      font-size: calc(16px + 2 * (100vw / 1200));
      line-height: normal;
    }
    .button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: $button-size;
      height: $button-size;
      border-radius: 50%;
      background: none;
      border: 1px solid rgba(0, 0, 0, 0);
      transition: all 0.15s;
      &:hover {
        background: rgba(0, 0, 0, 0.05);
        border: 1px solid rgba(0, 0, 0, 0.25);
      }
      &:active {
        translate: 0px 1px;
      }
    }
  }
  .modalEnter {
    opacity: 0;
  }
  .modalEnterActive {
    opacity: 1;
    transition: all 0.2s ease-in;
  }
  .modalExit {
    opacity: 1;
  }
  .modalExitActive {
    opacity: 0;
    transition: all 0.2s ease-out;
  }
}
@media (max-width: 480px) {
  .root {
    display: flex;
    flex-direction: column;
    height: unset;
    .counter {
      margin-left: unset;
    }
  }
}
